import React from 'react';

import styled from 'theme';

interface IWrapper {
  isGreen?: boolean;
}

const Wrapper = styled.div<IWrapper>`
  display: inline-block;
  padding: 3px 15px 2.7px;
  font-family: ${({ theme }) => theme.fonts.decorate};
  font-size: 11px;
  font-weight: 600;
  background-color:${({ isGreen, theme }) => isGreen ? theme.colors.green : theme.colors.lightAccent};
  border-radius: 7px;
  color: ${({ theme }) => theme.colors.white};
  text-transform: uppercase;
  letter-spacing: 0.6pt;
  box-shadow: ${({ theme }) => theme.shadows.aroundBox};
  user-select: none;
`;

const envName =  window.location.hostname.split('.')[0];
const isLocalhost = envName === 'localhost';

const EnvName: React.FC = () => {
  return (
    <Wrapper isGreen={isLocalhost}>
      {envName}
    </Wrapper>
  );
};

export default EnvName;
